<script setup>
  import { computed, onMounted, ref, h } from 'vue'
  import { usePage, router } from '@inertiajs/vue3'
  import { useStore } from 'vuex'
  import MainLayout from '@/Pages/Layouts/MainLayout.vue'
  import UEForm from '@/components/Form.vue'
  import { ALERT } from '@/store/mutations'

  const Store = useStore()

  // Define props
  const props = defineProps({
    formAttributes: {
      type: Object,
      default: () => ({}),
    }
  })

  const page = usePage()
  onMounted(() => {
    // console.info('Generic Form component mounted for:', page.props)
    console.info('Generic Form component mounted props for:', props.formAttributes)
  })

  const cancelPressRelease = () => {
    let endpoint = route('admin.press_release.update', {
      press_release: window.__data_get(props.formAttributes, 'modelValue.id')
    })

    axios.put(endpoint, {
      stateable_id: window.__data_get(props.formAttributes, 'modelValue._cancelledStatusId')
    }).then((response) => {
      if (response.status === 200) {
        router.reload({only: ['formAttributes']})

        Store.commit(ALERT.SET_ALERT, {
          ...response.data,
          message: 'Press release cancelled successfully',
        })

      }
    }).catch((error) => {
      console.error(error)
    })
  }

  defineOptions({
    layout: (h, page) => h(MainLayout, () => page),
    name: 'PressReleaseForm',
  })
</script>

<template>
  <div class="press-release-form">
    <ue-form
      v-bind="formAttributes"
    >
      <template v-if="formAttributes.isEditing" v-slot:header.left="headerLeftScope">
        <div v-if="$hasRoles(['superadmin', 'admin', 'manager', 'editor', 'reporter'])"
          class="d-flex flex-column gap-2"
        >
          <span class="text-body-1 font-weight-regular text-capitalize">
              {{ $t('Company') + ': ' + (headerLeftScope?.formItem?.company_name ?? '') }}
          </span>
          <span class="text-body-2 font-weight-regular text-grey text-capitalize">
              {{ $t('Project Name') + ': ' + (headerLeftScope?.formItem?.content?.alias ?? '') }}
          </span>
          <span class="text-body-2 font-weight-regular text-grey text-capitalize">
              {{ $t('PR') + '# ' + (headerLeftScope.model.id ?? '') }}
          </span>

          <div v-if="$hasRoles(['superadmin', 'admin', 'manager'])"
            class="mt-2"
          >
            <v-combobox
              v-model="headerLeftScope.model.authorized_id"
              v-bind="$lodash.pick(headerLeftScope.schema.authorized_id, [
                  'items',
                  'itemTitle',
                  'itemValue',
                  'clearable',
                  'disabled',
                  'readonly',
                  'rules',
                  'errorMessages',
                  'placeholder',
                  'label',
                  'hint',
                  'persistent-hint',
                  'disabled',
                  'readonly',
                  'rules',
                  'variant',
                  'returnObject'
              ])"
              style="max-width: 250px;text-transform: capitalize;"
              density="compact"
              hide-details
              :rules="[]"
            />
          </div>

          <span v-if="$hasRoles(['client-manager', 'client-assistant'])" class="text-body-2 font-weight-regular text-grey text-capitalize">
            {{ $t('PR') + '# ' + (headerLeftScope.model.id ?? '') }}
          </span>
        </div>
      </template>

      <template v-if="formAttributes.isEditing" v-slot:right.top="{isEditing, item, schema}">
        <v-card v-if="isEditing" variant="flat">
          <v-card-text class="pa-0">
            <!-- stateable state -->
            <ue-dynamic-component-renderer v-if="$hasRoles(['editor', 'reporter', 'client-manager', 'client-assistant'])"
              class="mb-4"
              :subject="item.state_formatted"
            >
            </ue-dynamic-component-renderer>

            <!-- Prices info -->
            <v-card v-if="$hasRoles(['superadmin', 'admin', 'manager', 'client-manager', 'client-assistant'])"
              class=""
              variant="outlined"
              color="grey-lighten-4"
            >
              <v-card-text class="d-flex flex-column gr-4">
                <ue-list-section v-if="item.press_release_packages && item.press_release_packages.length > 0"
                  :title="$t('Packages')"
                  :items="item.press_release_packages"
                  :item-fields="['packageable.name', 'name', 'base_price_raw_amount_formatted']"
                  :item-classes="['text-grey-darken-2']"
                  :col-classes="['text-wrap', '', 'text-end']"
                  :col-ratios="[3, 2, 2]"
                  title-classes="text-body-1 font-weight-medium text-grey-darken-4"
                ></ue-list-section>

                <ue-list-section v-if="item.press_release_package_addons && item.press_release_package_addons.length > 0"
                  :title="$t('Add-ons')"
                  :items="item.press_release_package_addons"
                  :item-fields="['name', 'base_price_raw_amount_formatted']"
                  :item-classes="['text-grey-darken-2']"
                  :col-classes="['', 'text-end']"
                  title-classes="text-body-1 font-weight-medium text-grey-darken-4"
                ></ue-list-section>

                <v-divider class=""></v-divider>

                <!-- Sub-Total -->
                <ue-list-section
                  :items="[
                    {title: $t('Sub-Total'), value: item.base_price_subtotal_amount_formatted},
                  ]"
                  :item-fields="['title', 'value']"
                  :item-classes="['text-grey-darken-2']"
                  :col-classes="['', 'text-end']"
                ></ue-list-section>

                <!-- Discount -->
                <ue-list-section v-if="item.base_price_has_discount"
                  class="mt-n4"
                  :items="[
                    {title: $t('Discount') + ` (${item.base_price_discount_percentage_formatted})`, value: `- ${item.base_price_total_discount_amount_formatted}`},
                  ]"
                  :item-fields="['title', 'value']"
                  :item-classes="['text-grey-darken-2']"
                  :col-classes="['', 'text-end']"
                ></ue-list-section>

                <!-- VAT -->
                <ue-list-section
                    :items="[
                        {title: $t('VAT') + ` (${item.base_price_vat_percentage_formatted})`, value: item.base_price_discounted_vat_amount_formatted},
                    ]"
                    :item-fields="['title', 'value']"
                    :item-classes="['text-grey-darken-2']"
                    :col-classes="['', 'text-end']"
                ></ue-list-section>

                <v-divider class=""></v-divider>

                <!-- Total -->
                <ue-list-section
                  :items="[
                    {title: $t('Total Amount'), value: item.base_price_total_amount_formatted},
                  ]"
                  :col-classes="['text-body-1 font-weight-medium text-grey-darken-4', 'text-end text-grey-darken-2']"
                  :item-fields="['title', 'value']"
                ></ue-list-section>
              </v-card-text>
            </v-card>

            <!-- Content and medias -->
            <v-card v-if="item.operation_list_sections && item.operation_list_sections.length > 0"
              class="mt-2"
              variant="outlined"
              color="grey-lighten-4"
              >
              <v-card-text class="d-flex flex-column gr-4">
                <ue-list-section
                  v-for="section in item.operation_list_sections ?? []"
                  title-classes="text-body-1 font-weight-medium text-grey-darken-4"
                  :item-classes="['text-grey-darken-2']"
                  v-bind="$lodash.omit(section, ['slots'])"
                >
                  <template v-for="(_slot, index) in section.slots ?? []"
                    :key="index"
                    v-slot:[_slot.name]="slotScope"
                  >
                    <template v-if="_slot.type == 'filepond' && slotScope.value">
                        <ue-filepond-preview :source="slotScope.value" show-inline-file-name image-size="24" />
                    </template>
                    <template v-else-if="_slot.type == 'html' && slotScope.value">
                        <fragment v-html="slotScope.value"></fragment>
                    </template>
                    <template v-else-if="_slot.type == 'date' && slotScope.value">
                        {{ slotScope.value ? $d(new Date(slotScope.value), 'numeric') : '' }}
                    </template>
                    <template v-else-if="slotScope.value">
                        {{ slotScope.value }}
                    </template>
                  </template>
                </ue-list-section>
              </v-card-text>
            </v-card>
          </v-card-text>

          <!-- Payment info -->
          <v-card v-if="$hasRoles(['admin', 'superadmin', 'manager']) && item.payment"
            class="mt-2"
            variant="outlined"
            color="grey-lighten-4"
          >
            <v-card-text class="d-flex flex-column gr-4">
              <ue-list-section
                :title="$t('Payments')"
                :items="[
                  {title: $d(new Date(item.payment.created_at), 'numeric'), service: item.payment.payment_gateway, value: item.payment.status},
                ]"
                :item-fields="['title', 'service', 'value']"
                :col-classes="['', 'text-center', 'text-end']"
                title-classes="text-body-1 font-weight-medium text-grey-darken-4"
                :item-classes="['text-grey-darken-2']"
              ></ue-list-section>
            </v-card-text>
          </v-card>

          <!-- payment invoices -->
          <v-card v-if="$hasRoles(['client-manager', 'client-assistant', 'admin', 'superadmin', 'manager']) && item.payment && item.payment.status == 'COMPLETED'"
            class="mt-2"
            variant="outlined"
            color="grey-lighten-4"
          >
            <v-card-text class="d-flex flex-column gr-4">
              <ue-list-section
                :title="$t('Invoice')"
                :items="[
                  {title: $d(new Date(item.payment.created_at), 'numeric'), value: item.payment.invoice_file},
                ]"
                :item-fields="['title', 'value']"
                :col-classes="['', 'text-end']"
                title-classes="text-body-1 font-weight-medium text-grey-darken-4"
                :item-classes="['text-grey-darken-2']"
              >
                <template v-slot:field.1="slotScope">
                  <ue-filepond-preview v-if="slotScope.value && slotScope.value.source"
                    :source="slotScope.value"
                    show-inline-file-name
                    image-size="24"
                  />
                </template>
              </ue-list-section>
            </v-card-text>
          </v-card>


        </v-card>
      </template>

      <!-- Cancel Form -->
      <template v-if="formAttributes.isEditing && $hasRoles(['client-manager', 'client-assistant', 'superadmin'])"
        v-slot:right.bottom="{isEditing, item, schema}"
      >
        <ue-modal v-if="item._cancelledStatusId && ['in-review', 'pending-payment'].includes(window.__data_get(item, 'state.code'))"
          :confirm-callback="cancelPressRelease"
          :use-model-value="false"
          :reject-button-attributes="{
            color: 'info',
            variant: 'outlined'
          }"
          :confirm-button-attributes="{
            color: 'secondary',
            variant: 'flat'
          }"
          :title="$t('fields.confirm-cancellation-title')"
          :cancel-text="$t('fields.no')"
          :confirm-text="$t('fields.yes')"
          titleJustify="center"
        >
          <template v-slot:activator="{ props }">
              <v-btn class="justify-start my-2" color="error" prepend-icon="mdi-close-circle-outline" variant="outlined" block v-bind="props">
                  {{ $t('fields.cancel') }}
              </v-btn>
          </template>
          <template v-slot:body.description="{ props }">
              {{ $t('fields.confirm-cancellation-description') }}
          </template>
        </ue-modal>
      </template>

      <template v-slot:actions.append="actionsScope">
        <!-- Download Reports -->
        <ue-modal v-if="$hasRoles(['client-manager', 'client-assistant', 'superadmin']) && ['distributed', 'completed'].includes(actionsScope.item?.state?.code ?? '_unstate')
          && (actionsScope.isEditing
            || actionsScope.item.daily_report.length > 0
            || actionsScope.item.weekly_report.length > 0
            || actionsScope.item.monthly_report.length > 0)"
          :use-model-value='false'
          :title="$t('Download Reports')"
          has-close-button
          no-actions
          description-body-class=""
        >
          <template v-slot:activator="activatorScope">
            <v-tooltip :text="$t('Download Reports')" location="top">
              <template v-slot:activator="{props}">
                <v-btn variant="elevated" v-bind="{...activatorScope.props, ...props}" icon="mdi-chart-line"
                  density="compact" :rounded="true" />
              </template>
            </v-tooltip>
          </template>
          <template #body.description>
            <div class="d-flex flex-column gap-2">
              <template v-if="actionsScope.item.daily_report.length > 0">
                <div class="text-body-1 font-weight-medium mb-4 d-flex justify-start">
                  {{ $t('Daily Report') }}</div>
                <ue-filepond-preview :source="actionsScope.item.daily_report" show-inline-file-name image-size="24"
                  max-file-name-length="50" class="mb-4" />
              </template>
              <template v-if="actionsScope.item.weekly_report.length > 0">
                <div class="text-body-1 font-weight-medium mb-4 d-flex justify-start">
                  {{ $t('Weekly Report') }}</div>
                <ue-filepond-preview :source="actionsScope.item.weekly_report" show-inline-file-name image-size="24"
                  max-file-name-length="50" class="mb-4" />
              </template>
              <template v-if="actionsScope.item.monthly_report.length > 0">
                <div class="text-body-1 font-weight-medium mb-4 d-flex justify-start">
                  {{ $t('Monthly Report') }}</div>
                <ue-filepond-preview :source="actionsScope.item.monthly_report" show-inline-file-name image-size="24"
                  max-file-name-length="50" class="mb-4" />
              </template>
            </div>
          </template>
        </ue-modal>
      </template>
    </ue-form>
  </div>
</template>

<style scoped>

</style>

