<script setup>
  import { computed, h } from 'vue'
  import { useI18n } from 'vue-i18n'
  import { usePage, router } from '@inertiajs/vue3'
  import { route} from '#/vendor/tightenco/ziggy'

  import { useUser } from '@/hooks'
  import MainLayout from '@/Pages/Layouts/MainLayout.vue'

  const { t } = useI18n()
  const page = usePage()

  const {isGuest} = useUser()

  const props = defineProps({
    submitAttributes: {
      type: Object,
      required: true,
    }
  })

  const generatePackagePricesSections = (packageables, pressReleasePackagesModel) => {
    let result = []

    Object.entries(pressReleasePackagesModel).forEach(([locationId, object]) => {

      try {
        let packageable = packageables.find(p => p.id == locationId)
        let pkg = packageable.vendable_packages.find(p => p.id == object.package_id)

        result.push({
          title: packageable.name,
          value: pkg.base_price_raw_amount_formatted ?? ''
        })
      } catch (error) {

      }

    })

    return result
  }

  const generateAddonPricesSections = (selecteds, lastFormPreview) => {
    let result = []

    let selectedIds = selecteds.map(s => s.package_addon_id)
    selectedIds.forEach(selectedId => {
      let addon = lastFormPreview.find(item => item.id == selectedId && item.fieldName == 'pressReleasePackageAddons')
      if(addon){
        result.push({
          title: addon.name,
          value: addon.base_price_raw_amount_formatted ?? ''
        })
      }
    })
    return result
  }

  const generateSections = (schemas, models, lastFormPreview, lastStepModel) => {
    let result = []

    if(models[1] && models[1].pressReleasePackages && Object.keys(models[1].pressReleasePackages).length > 0){
        result.push({
            title: t('Packages'),
            items: generatePackagePricesSections(schemas[1].pressReleasePackages.items, models[1].pressReleasePackages)
        })
    }

    if(Array.isArray(lastStepModel['pressReleasePackageAddons']) && lastStepModel['pressReleasePackageAddons'].length > 0){
        let selectedAddons = lastStepModel['pressReleasePackageAddons']

        result.push({
            title: t('Add-Ons'),
            items: generateAddonPricesSections(selectedAddons, lastFormPreview)
        })
    }
    return result
  }

  const extraErrorCardAttributes = computed(() => {
    if(isGuest.value){
      return {
        homeUrl: route('admin.login'),
        homeText: t('Login'),
      }
    }

    return {}
  })

  defineOptions({
    layout: (h, page) => h(MainLayout, () => page),
    name: 'PressReleaseSubmit',
  })

</script>

<template>
  <v-container class="d-sm-none">
    <v-row justify="center" align="center" class="fill-height">
      <v-col cols="12" md="8" lg="6">
        <ue-error-card
          icon="mdi-alert-circle-outline"
          status-code="403"
          status-text="Access Forbidden"
          description="Sorry, you are not allowed to access this page on mobile devices. Please use a desktop device to continue."
          alert="warning"
          v-bind="extraErrorCardAttributes"
        >
        </ue-error-card>
      </v-col>
    </v-row>
  </v-container>

  <template v-if="submitAttributes.submitType === 'stepper'">
    <ue-stepper-form
      v-bind='submitAttributes.stepperFormAttributes'
      class="pa-3 d-none"
      flex-breakpoint="sm"
    >
      <!-- Summary Final Body -->
      <template v-slot:summary.final.body="{models, schemas, lastStepModel, lastFormPreview}">
        <template v-for="(section, index) in generateSections(schemas, models, lastFormPreview, lastStepModel)">
          <ue-title
            :text="$t(section.title)"
            transform="capitalize"
            type="h6"
            color="white"
            padding="x-6"
            margin="t-6"
            class="mx-n6 py-3 bg-primary-darken-1"
          >
          </ue-title>
          <ue-list-section
            class="bg-transparent my-3"
            item-classes="text-body-1 font-weight-bold"
            :items="section.items"
            :item-fields="['title', 'value']"
            :col-classes="['text-wrap', 'd-flex justify-end']"
            :col-ratios="[2,1]"
          >
          </ue-list-section>
        </template>
      </template>

      <!-- Summary Final Total -->
      <template v-slot:summary.final.total='finalTotalScope'>
        <ue-print-request
          :payload="finalTotalScope.payload"
          :endpoint='route("press_release.calculate-price")'
          :print-keys="[
            { text: $t('total'), subText: $t('vatText') }
          ]"
          :loading-text="$t('Calculating...')"
        />
      </template>

      <!-- Summary Final Description -->
      <template v-slot:summary.final.description>
        {{ $t('Once you complete your request, we will review your content and if approved, you will receive a payment notification.')}}
      </template>
    </ue-stepper-form>
  </template>

  <template v-else-if="submitAttributes.submitType === 'package-selector'">
    <div class="d-none d-sm-block h-100">
      <ue-custom-package-selector v-bind='submitAttributes.packageSelectorAttributes'/>
    </div>
  </template>
</template>

<style lang="sass">
  @media (max-width: 959px)
    #package-selector-empty-image
      display: none !important
</style>
